<template>
  <div class="clearfix">
    <div class="titCon inner">
      <div class="tit">
        <em>{{title}}</em>
        <span></span>
      </div>
      <span class="more">
        <router-link to="/gameList">更多</router-link>
      </span>
    </div>
    <div class="tdRecom inner">
      <el-row>
        <el-col :xs="12" :sm="6" :md='4' v-for="(v,i) in gameList" :key="i">
          <div>
            <router-link to="/">
              <img v-lazy="v.url" :alt="v.name">
              <p>{{v.name}}</p>
            </router-link>
            <router-link :to="{path:'/detail', query:{'id':v.id}}" class="viewBtn">查看</router-link>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    gameList: {
      type: Array,
      default: () => []
    },
    title: {
      type: String,
      default: () => '推荐'
    }
  }
}
</script>
<style lang="scss" scoped>
.titCon{
  width: 100%;
  padding: 0 10px;
  border-bottom: 1px solid #eeeeee;
  span.more{
    float: right;
    font-size: 12px;
    margin:2px 1px 0 0;
    a{
      color: #18ac3c;
    }
  }
  .tit{
    width: 64px;
    height: 30px;
    position: relative;
    float: left;
    margin-bottom: 6px;
    em{
      font-style: normal;
      width: 64px;
      text-align: center;
      display: block;
    }
    span{
      height: 3px;
      width: 10px;
      background: #333;
      margin: 0 auto;
      display: block;
      position: absolute;
      top: 26px;
      left: 50%;
      margin-left: -5px;
    }
  }
  &:after{
    content:' ';
    display:table;
    clear:both;
  }
}
.tdRecom{
  margin-bottom: 60px;
  .el-col{
    list-style: none;
    font-size: 12px;
    div{
      margin: 48px 40px 0;
      width: 80px;
      img{
        width: 62px;
      }
      p{
        color: #333;
        max-width: 80px;
        overflow: hidden;
        text-align: center;
        text-overflow: ellipsis;
        white-space: nowrap;
        margin: 8px 0 4px;
      }
      .viewBtn{
        width: 60px;
        height: 24px;
        font-size: 12px;
        line-height: 24px;
        background: #18ac3c;
        border-radius: 3px;
        color: white;
        margin: 0 auto;
        display: block;
      }
    }
  }
}
</style>



