<template>
  <div class="bannerCon">
    <div class="inner">
      <el-row class="innerCon">
        <el-col :sm="12" :md="12">
          <div class="pcCon">
            <img src="@/assets/img/bg.jpg" alt="诠星游戏平台">
          </div>
        </el-col>
        <el-col :sm="12" :md="12">
          <div class="qrCon">
            <h4>在这里找到想要的游戏</h4>
            <p>玩游戏？快来诠星游戏平台</p>
            <div class="qr">
              <img src="@/assets/img/qr2.png" alt="安卓下载">
              <img src="@/assets/img/qr2.png" alt="ios下载">
            </div>
            <div class="btnCon">
              <el-button>Android版下载</el-button>
              <el-button>IOS版下载</el-button>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
export default {
  data: function () {
    return {
      name: '123'
    }
  }
}
</script>
<style lang="scss" scoped>
.bannerCon{
  margin: 50px 0;
  position: relative;
  .innerCon{
    // width: 960px;
    height: 100%;
    margin: 0 auto;
    position: relative;
  }
  .pcCon{
    width: 100%;
    height: 100%;
    margin-top: 40px;
    img{
      width: 100%;
    }
  }
  .qrCon{
    margin-top: 100px;
    h4{
      font-weight: normal;
      font-size: 24px;
      text-align: center;
    }
    p{
      font-size: 12px;
      text-align: center;
      color: #9b9794;
    }
    .qr{
      margin-top: 30px;
      img{
        height: 114px;
      }
      img+img{
        margin-left: 20px;
      }
    }
    .btnCon{
      margin-top: 46px;
      .el-button{
        background-color: #18ac3c;
        color: white;
        width: 150px;
        height: 48px;
      }
    }
  }
}
</style>



