<template>
<div>
  <div class="logoCon">
    <div class="inner">
      <div class="logoConL">
        <div class="logo" :style="{backgroundImage:'url(' + bgIcon + ')'}"></div>
        <div class="name">诠星游戏平台</div>
      </div>
      <div class="logoConR">
        <div class="search">
          <input type="text" v-model="searchVal" @keyup.enter="search()" clearable placeholder="搜索游戏">
          <i class="el-icon-ali-search" @click="search()"></i>
        </div>
      </div>
    </div>
  </div>
  <div class="navCon">
    <div class="inner">
      <b-nav class="navs">
        <b-nav-item v-for="(v,i) in navs" :key="i" :class="{active: i===isActive }" :to="v.url" @click="getNavIndex(i)">
          <span>{{v.name}}</span>
        </b-nav-item>
      </b-nav>
    </div>
  </div>
  <router-view/>
  <footerCom/>
</div>
</template>

<script>
import footerCom from '@/components/footer'
import bgIcon from '@/assets/img/icon.png'
import { mapGetters } from 'vuex'
export default {
  name: 'HelloWorld',
  components: {
    footerCom
  },
  created () {
    this.listenNav()
  },
  computed: {
    ...mapGetters(['navIndex'])
  },
  watch: {
    navIndex (a, b) {
      console.log('watch----' + a)
      this.isActive = a
    },
    $route (a, b) {
      this.listenNav()
    }
  },
  data () {
    return {
      bgIcon: bgIcon,
      isActive: 0,
      searchVal: '',
      navs: [
        {name: '首页', url: '/'},
        {name: '游戏', url: '/gameList'},
        {name: '排行榜', url: '/topList'}
      ]
    }
  },
  methods: {
    getNavIndex (v) {
      this.isActive = v
      this.$store.dispatch('ChangeNavIndex', v)
    },
    listenNav () {
      this.isActive = -1
      this.navs.map((v, i) => {
        if (v.url === this.$route.path) {
          this.isActive = i
        }
      })
    },
    search () {
      this.$router.push({path: '/search', query: {searchVal: this.searchVal}})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
.logoCon{
  height: 80px;
  overflow: hidden;
  .logoConL{
    line-height: 80px;
    margin-left: 35px;
    .logo{
      width: 51px;
      margin: 15px 10px;
      height: 51px;
      float: left;
      background-size: 51px;
      background-repeat: no-repeat;
    }
    .name{
      float:left;
      font-size: 28px;
      font-weight: bold;
      color: #555;
    }
  }
  .logoConR{
    text-align: right;
    float: right;
    margin-right: 40px;
    .search{
      height: 40px;
      width: 206px;
      margin-top: 20px;
      border-radius: 4px;
      border: 1px solid #eeeeee;
      input{
        line-height: 36px;
        border: none;
        float: left;
        outline: none;
        height: 36px;
        outline: 0;
        padding: 3px 0 3px 8px;
        vertical-align: middle;
        width: 174px;
        color:#555;
        font-size: 14px;
      }
      i{
        font-size: 22px;
        color: #3d3d3d;
        line-height: 32px;
        padding: 3px;
      }
    }
  }
}
.navCon{
  border-bottom: 1px solid #eeeeee;
  .navs{
    margin-left: 86px;
    li{
      text-align: center;
      margin-right:55px;
      // margin-bottom: -3px;
      a{
        width: 69px;
        color: #666;
        font-size: 14px;
        padding: 3px 4px;
        margin-bottom: 3px;
        span{
          margin-bottom: -3px;
        }
      }
      &.active{
        border-bottom: 3px solid #4cc8b5;
      }
    }
  }
}

@media screen and ( max-width:516px )  {
  .logoCon{
    .logoConL{
      margin-left: 0;
      line-height: 60px;
      .logo{
        background-size: 30px;
        width: 30px;
        margin: 15px 10px;
        height: 30px;
      }
      .name{
        font-size: 18px;
      }
    }
    .logoConR{
      margin-right: 10px;
      .search{
        height: 30px;
        width: 130px;
        margin-top: 15px;
        input{
          line-height: 28px;
          height: 28px;
          font-size: 12px;
          width: 105px;
        }
        i{
          font-size: 16px;
          line-height: 24px;
        }
      }
    }
  }
  .navCon .navs{
    margin-left: 50px;
  }
}
@media screen and ( max-width:424px ) {
  .navCon .navs{
    margin-left: 22px;
    li{
      margin-right: 30px;
    }
  }
}
</style>



